import { getImagesByQuery, PER_PAGE } from "./js/pixabay-api.js";

import {
  createGallery,
  clearGallery,
  showLoader,
  hideLoader,
  showLoadMoreButton,
  hideLoadMoreButton,
} from "./js/render-functions.js";



const form = document.querySelector(".form");
const input = form.querySelector('input[name="search-text"]');
const loadMoreBtn = document.querySelector("#load-more");

let query = "";
let page = 1;
let totalHits = 0;
let loaded = 0;
let isLoading = false;


hideLoadMoreButton();


form.addEventListener("submit", async (e) => {
  e.preventDefault();
  const value = input.value.trim();
  if (!value) return;


  query = value;
  page = 1;
  loaded = 0;
  clearGallery();
  hideLoadMoreButton();

  await fetchAndRender();
});


loadMoreBtn.addEventListener("click", async () => {
  page += 1;
  await fetchAndRender(true);
});

async function fetchAndRender(isLoadMore = false) {
  try {
    if (isLoading) return;
    isLoading = true;
    showLoader();

    const data = await getImagesByQuery(query, page);
    const hits = data.hits ?? [];
    totalHits = data.totalHits ?? 0;


    if (!isLoadMore && hits.length === 0) {
      hideLoadMoreButton();
      alert("–ù—ñ—á–æ–≥–æ –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ. –°–ø—Ä–æ–±—É–π —ñ–Ω—à–∏–π –∑–∞–ø–∏—Ç.");
      return;
    }

    createGallery(hits);
    loaded += hits.length;


    if (loaded < totalHits) {
      showLoadMoreButton();
    } else {
      hideLoadMoreButton();
      if (loaded > 0) {
  
        console.log("We're sorry, but you've reached the end of search results.");
        alert("We're sorry, but you've reached the end of search results.");
      }
    }

    if (isLoadMore) smoothScrollAfterAppend();

  } catch (err) {
    console.error(err);
    alert("–°—Ç–∞–ª–∞—Å—è –ø–æ–º–∏–ª–∫–∞ –ø—ñ–¥ —á–∞—Å –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –∑–æ–±—Ä–∞–∂–µ–Ω—å.");
    hideLoadMoreButton();
  } finally {
    hideLoader();
    isLoading = false;
  }
}
